import React, { useEffect, useState, memo } from "react";
import { NavLink, Link } from "react-router-dom";
import AdminDashboardView from "../../../../PageComponent/Admin/Dashboard/AdminDashboardView";
import Header from "../../partials/Header";
import { useDispatch, useSelector } from "react-redux";
// trasnlation library
import { useTranslation } from "react-i18next";
import {DeleteAdminBySuperAdmin} from './../../../../entity/Redux/Admin/Action'
import SideBar from "../Partials/SideBar";
import $ from "jquery";
import {
  AddNewUser,
  GetAllUser,
} from "./../../../../entity/Redux/Admin/Action";
export default memo(() => {
  const [userData, setUserData] = useState();
  const dispatch = useDispatch();
  const { t, i18n } = useTranslation(); // translate functional

  const states = useSelector((state) => state.Admin);

  useEffect(() => {
    dispatch(GetAllUser());
  }, []);
  {
    /*
    -- language
    -- do zabaneh kardan site
    */
  }

  function handleLang(e) {
    // e.preventDefault();
    // sabt type language dar storage
    if (!localStorage.getItem("lan")) {
      localStorage.setItem("lan", "en");
    }
    //daryaft type language as localstorage
    var lan = localStorage.getItem("lan");
    var ckeckbox = $("input[name=cahngelan]");
    if (ckeckbox.is(":checked") && lan === "en") {
      localStorage.setItem("lan", "ch");
    } else {
      localStorage.setItem("lan", "en");
    }
    let lang = localStorage.getItem("lan");
    // gharar dadan language dar library translate
    i18n.changeLanguage(lang);
  }

  // end language

  const setData = (e) => {
    setUserData({
      ...userData,
      [e.target.name]: e.target.value,
    });
  };

  {
    /*
        -- submit data user
        -- dispatch function newdata baraye sabt etelaat karbar
    */
  }

  function submitUserDetail(e) {
    e.preventDefault();
    dispatch(AddNewUser(userData));
  }

  // end submit data user

  const renderUserAdmin = (data) => {
    return <div></div>;
  };


  const DeleteUser = (e , id)=>{
    e.preventDefault()
    dispatch(DeleteAdminBySuperAdmin(id))
  }
  return (
    <div>
      <Header />

      {console.log("state : ", states)}
      <div id="main">
        <div className="wrapper">
          <SideBar />

          <div id="back_shado"></div>

          <section id="content">
            <div className="container">
              <div id="work-collections">
                <div className="row">
                  <div className="col s12">
                    <div id="breadcrumbs-wrapper">
                      <div className="container">
                        <div className="row">
                          <div className="col s10 m6 l6">
                            <h5 className="breadcrumbs-title">Create User</h5>
                            <ol className="breadcrumbs">
                              <li>
                                <NavLink to="/admin-top">
                                  <a>Dashboard</a>
                                </NavLink>
                              </li>

                              <li>
                                <a className="active">Create User</a>
                              </li>
                            </ol>
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>

                  <div id="work-collections">
                    <div className="col s12 m12 l6">
                      <ul
                        id="projects-collection"
                        className="collection z-depth-1"
                      >
                        <li className="collection-item avatar">
                          <i className="material-icons" id="custom_icon">
                            person_add
                          </i>
                          <h6 className="collection-header m-0">Add User</h6>
                          <p>Create new admin user</p>
                        </li>

                        {/*
                                                -- form data
                                                -- form atelat avalie karbar
                                            */}

                        <form
                          onSubmit={(e) => submitUserDetail(e)}
                          style={{ background: "#212121" }}
                        >
                          <div className="container">
                            <div className="row">
                              <div className="col s12">
                                <div className="row"></div>

                                {/*
                input marbout be fullname doctor
            */}
                                <div
                                  className="input-field col s12"
                                  style={{ marginTop: 0 }}
                                >
                                  <p style={{ color: "#ccc" }}>
                                    &nbsp;&nbsp;{t("UserName.l")}
                                  </p>
                                  <div id="input_back">
                                    <input
                                      placeholder={"Enter UserName ..."}
                                      autoComplete="false"
                                      name="phoneNumber"
                                      onChange={(e) => setData(e)}
                                      required
                                      type="text"
                                      className="validate"
                                    />
                                  </div>
                                  <p id="alert-p">
                                    &nbsp;&nbsp;{t("must-field.l")}
                                  </p>

                                  {/*<label htmlFor="password">Password</label>*/}
                                </div>

                                {/*
                input marbout be password doctor
            */}
                                <div
                                  className="input-field col s12"
                                  style={{ marginTop: 0 }}
                                >
                                  <p style={{ color: "#ccc" }}>
                                    &nbsp;&nbsp;{t("Password.l")}
                                  </p>
                                  <div id="input_back">
                                    <input
                                      name="password"
                                      placeholder={"Enter Password ..."}
                                      autoComplete="false"
                                      name="password"
                                      onChange={(e) => setData(e)}
                                      required
                                      type="password"
                                      className="validate"
                                    />
                                  </div>
                                  <p id="alert-p">
                                    &nbsp;&nbsp;{t("must-field.l")}
                                  </p>
                                </div>

                                <div
                                  className={`col s12`}
                                  style={{ marginTop: 15, marginBottom: 30 }}
                                >
                                  <input
                                    className="btn blue darken-4 waves-effect"
                                    type="submit"
                                    id="check_phone_num"
                                    value={t("Save.l")}
                                  />
                                  {/*</Link>*/}
                                </div>
                              </div>
                            </div>
                          </div>
                        </form>

                        {/*    end form data   */}
                      </ul>
                    </div>

                    <div className="col s12 m12 l6">
                      <ul
                        id="projects-collection"
                        className="collection z-depth-1"
                      >
                        <li className="collection-item avatar">
                          <i className="material-icons" id="custom_icon">
                            group
                          </i>
                          <h6 className="collection-header m-0">Users</h6>
                          <p>List of all admin user</p>
                        </li>

                        {states.UserAdmin
                          ? console.log(
                              "bbbbbbb : ",
                              states.UserAdmin.data.data
                            )
                          : ""}
                        <table style={{ background: "#222222" }}>
                          <tbody>
                            {states.UserAdmin
                              ? states.UserAdmin.data.data.map((item) =>  (
                                    <tr>
                                      <td>{item.phoneNumber}</td>
                                      <td>
                                        <a
                                            onClick={e => DeleteUser( e , item.id)}
                                            className={
                                              "btn waves-effect red darken-4"
                                            }
                                            style={{ textTransform: "unset" , marginRight : 5 }}
                                        >
                                          Delete
                                        </a>
                                        <a
                                      
                                          className={
                                            "btn waves-effect green darken-4"
                                          }
                                          style={{ textTransform: "unset" }}
                                        >
                                          Setting
                                        </a>
                                      </td>
                                    </tr>
                                  ))
                              : "please wait ..."}
                          </tbody>
                        </table>
                      </ul>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </section>
        </div>
      </div>
    </div>
  );
});
